import React from "react";
import SwipeableDrawer from "@material-ui/core/SwipeableDrawer";
import "./style.css";
const SwipeableContainer = ({ is_open, handleGuide2 }) => {
  const handleClose = () => handleGuide2(false);
  return (
    <div className="partials">
      <SwipeableDrawer
        allowSwipeInChildren={true}
        PaperProps={{
          style: {
            width: "100vw",
            minHeight: "50vh",
            maxHeight: "50vh",
          },
        }}
        anchor="bottom"
        open={is_open}
        onClose={handleClose}
      >
        <div className="drawer-content">
          <div className="drawer-header">
            {/* Static header */}
            <div
              style={{ padding: "1rem" }}
              className="bg-dark offcanvas-header"
            >
              <div className="text-white fw-bold offcanvas-title h5">
                Updated Game Rules (from 12th June 2023)
              </div>
              <button
                onClick={handleClose}
                type="button"
                className="btn-close btn-close-white"
                aria-label="Close"
              ></button>
            </div>
          </div>

          {/* Content */}
          <div className="offcanvas-body" style={{ padding: "1rem" }}>
            <ul className="list-group mb-3">
              <li className="list-group-item text-start">
                  Join    Opponent        
                           {" "}
                <span style={{ color: "red" }}>30% loss</span>
                   !     Autoexit     
                100% Loss    !       
                        
              </li>
              <li className="list-group-item text-start">
                          {" "}
                <span style={{ color: "red" }}>30% loss</span>
                         2     
                      100% Loss    !
              </li>
              <li className="list-group-item text-start">
                Autoexit   1          
                           Loss  
                    Admin   !
              </li>
              <li className="list-group-item text-start">
                     Opponent     Autoexit 
                   Admin         
                          !  
                            
                          !
              </li>
              <li className="list-group-item text-start">
                Game    15       
                 Opponent          
                           
                         !
              </li>
            </ul>

            <ul className="list-group mb-3">
              <li className="list-group-item text-start">
                Win             Cancel
                            
                Live Chat           
                   !
              </li>
              <li className="list-group-item text-start">
                    ()         
                      ! [     
                 ]
              </li>
              <li className="list-group-item text-start">
                ''            
                          
                        
              </li>
              <li className="list-group-item text-start">
                             
                           
                         !
              </li>
            </ul>
          </div>
        </div>
      </SwipeableDrawer>
    </div>
  );
};

export default SwipeableContainer;
